@charset "UTF-8";

* {
  margin: 0;
  padding: 0;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  font-size: 18px;
}

#container {
  display: flex;
  align-items: center;
  padding-top: 50px;
}

#profile-area {
  width: 30%;
  text-align: center;
}

#profile-area img {
  width: 50%;
}

#todo-area {
  width: 70%;
}

.balloon{
  position: relative;
  width: 50%;
  margin: 0 auto;
  padding: 10px;
  background-color: #ccffcc;
  box-shadow: 0px 0px 3px 0px #cccccc;
}

.balloon::before{
  content: '';
  position: absolute;
  display: block;
  width: 15px;
  height: 15px;
  left: 20%;
  top: -15px;
  border-bottom: 15px solid #ccffcc;
  border-left: 15px solid transparent;
}

.done {
  color: red;
  text-decoration: line-through;
}

.input-todo {
  height: 3rem;
  width: 400px;
  padding: 0.5rem;
  font-size: 1rem;
  margin-bottom: 2rem;
}

.todo-list input[type="checkbox"] {
  width: 1rem;
  height: 1rem;
}

.todo-list {
  list-style: none;
}

.todo-list li span {
  margin-left: 1rem;
  margin-bottom: 1rem;
}

@media screen and (max-width:970px) {
  #container {
    flex-direction: column;
  }
  #profile-area {
    width: 100%;
  }
  #todo-area {
    width: 100%;
    text-align: center;
    margin-top: 2rem;
  }
}
